import { postFormData, post } from './ApiSender';
import { ResourceApi } from './constant';

export function uploadImage(payload) {
    return postFormData(ResourceApi.uploadImage, payload);
}

export function removeImage(payload) {
    return post(ResourceApi.removeImage, payload);
}

export function setToMainImage(payload) {
    return post(ResourceApi.setToMainImage, payload);
}
