import { post } from './ApiSender';
import { HotelApi } from './constant';

export function createHotel(payload) {
    return post(HotelApi.createHotel, payload);
}

export function getHotelInfo(payload) {
    return post(HotelApi.getHotelInfo, payload);
}

export function updateHotelInfo(payload) {
    return post(HotelApi.updateHotelInfo, payload);
}

export function getRoomTypes(payload) {
    return post(HotelApi.getRoomTypes, payload);
}

export function updateAmenities(payload) {
    return post(HotelApi.updateAmenities, payload);
}

export function getHotelImages(payload) {
    return post(HotelApi.getHotelImages, payload);
}

export function getHotelSetting(payload) {
    return post(HotelApi.getHotelSetting, payload);
}

export function updateHotelSetting(payload) {
    return post(HotelApi.updateHotelSetting, payload);
}