import React, { useState, useEffect } from 'react';
import { Upload, Icon, message, Button, Divider, Card, Row, Col, Checkbox } from 'antd';
import { getHotelImages } from '../../api/HotelApi';
import { uploadImage, removeImage, setToMainImage } from '../../api/ResourceApi';
import { BaseUrl } from '../../api/ApiSender';
import SlideImageModal from '../../components/SlideImageModal';

const Dragger = Upload.Dragger;
const { Meta } = Card;

export default function HotelImages(props) {
  const [file, setFile] = useState(undefined);
  const [fileList, setFileList] = useState([]);
  const [uploading, setUploading] = useState(false);
  const [images, setImages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [selectedImageIdx, setSelectedImageIdx] = useState({});

  useEffect(() => {
    if (props.hotelId) {
      fetchImages(props.hotelId);
    }
  }, [props.hotelId])

  const fetchImages = async (id) => {
    getHotelImages({ hotelId: id, paging: { currentPage: 0, pageSize: 50} }).then(res => {
      if (res.status === 'SUCCESS' && res.value) {
        setImages(res.value);
      }
    })
  }

  const onClickShowModal = (index) => {
    setSelectedImageIdx(index);
    setShowModal(true);
  }

  const onCloseModal = () => {
    setShowModal(false);
  }

  const handleUpload = () => {
    if (!props.hotelId) return;

    const formData = new FormData();
    formData.append('hotelId', props.hotelId);
    formData.append('image', file);

    setUploading(true);
    uploadImage(formData).then(res => {
      setFile(undefined);
      setFileList([]);
      setUploading(false);
      if (res.status === 'SUCCESS') {
        fetchImages(props.hotelId);
        message.success('Tải lên thành công');
      } else {
        message.error(res.value || 'Tải lên không thành công');
      }
    }).catch(err => {
      setUploading(false);
      message.error(err.message);
    })
  }

  const onChangeCheckboxMainImage = (e, idx) => {
    const data = [...images];
    data.map((item, index) => {
      if (index === idx && e.target.checked) {
        item.mainImage = true;
      } else {
        item.mainImage = false;
      }
    })
    setImages(data);
  }

  const onSelectImage = (e, idx) => {
    const data = [...images];
    data[idx].selected = e.target.checked;
    setImages(data);
  }

  const onClickDeleteBtn = () => {
    const selectedImages = [];
    images.map(item => {
      if (item.selected) {
        selectedImages.push({imageId: item.imageId});
      }
    });
    if (selectedImages.length === 0) return;

    const confirmDelete = window.confirm('Bạn có chắc chắn muốn xóa ảnh này?');
    if (!confirmDelete) return;

    setLoading(true);
    removeImage(selectedImages).then(res => {
      setLoading(false);
      if (res.status === 'SUCCESS') {
        fetchImages(props.hotelId);
        message.success('Xóa ảnh thành công');
      } else {
        message.error(res.value || 'Xóa ảnh không thành công');
      }
    }).catch(err => {
      setLoading(false);
      message.error(err.message);
    })
  }

  const onClickSaveBtn = () => {
    let mainImage = 0;
    images.map(item => {
      if (item.mainImage) {
        mainImage = item.imageId;
      }
    });
    if (mainImage === 0) return;

    setLoading(true);
    setToMainImage({imageId: mainImage}).then(res => {
      setLoading(false);
      if (res.status === 'SUCCESS') {
        message.success('Lưu ảnh thành công');
      } else {
        message.error(res.value || 'Lưu ảnh không thành công');
      }
    }).catch(err => {
      setLoading(false);
      message.error(err.message);
    })
  }

  const options = {
    name: 'image',
    accept: ".jpg, .jpeg, .png, .gif",
    listStyle: 'picture',
    multiple: false,
    beforeUpload(data) {
      setFileList([data]);
      setFile(data);
      return false;
    },
    fileList
  };

  return (
    <div>
      <Dragger {...options}>
        <p className="ant-upload-drag-icon">
          <Icon type="inbox" />
        </p>
        <p className="ant-upload-text">Click hoặc kéo thả ảnh vào đây để tải lên</p>
        {/* <p className="ant-upload-hint">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p> */}
      </Dragger>
      <Row style={{ marginTop: '16px' }}>
        <Button
            type="primary"
            onClick={handleUpload}
            disabled={file === undefined}
            loading={uploading}
          >
            {uploading ? 'Đang tải lên' : 'Tải lên' }
        </Button>
        <div style={{ float: 'right' }}>
          <Button type='danger' 
            style={{ marginRight: '5px' }} 
            onClick={onClickDeleteBtn} 
            loading={loading}
          >
            Xóa
          </Button>
          <Button type='primary' 
            onClick={onClickSaveBtn}
            loading={loading}
          >
            Lưu
          </Button>
        </div>
      </Row>
      <Divider orientation="left">Hình ảnh đã tải lên</Divider>
      <Row gutter={16} >
        {
          images && images.map((item, index) => {
            return (
              <Col xs={24} sm={8} md={6} lg={4} key={index} style={{ marginBottom: '16px' }}>
                <Card
                  hoverable
                  bordered
                  style={{ position: 'relative' }}
                  cover={<img alt="thumb" src={BaseUrl + item.thumbUrl} onClick={() => onClickShowModal(index)} />}
                >
                  <Meta
                    title={
                      <Checkbox checked={item.mainImage} onChange={(e) => onChangeCheckboxMainImage(e, index)}>
                        Ảnh chính
                      </Checkbox>
                    }
                    description={
                      <Checkbox style={{ position: 'absolute', right: '10px', top: '10px', transform: 'scale(1.5)' }}
                        checked={item.selected}
                        onChange={(e) => onSelectImage(e, index)}
                      />
                    }
                  />
                </Card>
                </Col>
            )
          })
        }
      </Row>
      <SlideImageModal showModal={showModal} 
        onCloseModal={onCloseModal} 
        images={images} 
        selectedImageIdx={selectedImageIdx} 
      />
    </div>
  )
}
