import React, { useEffect } from 'react';
import { Modal, Carousel, Icon } from 'antd';
import { BaseUrl } from '../api/ApiSender';
import styles from './styles/SlideImageModal.module.scss';

export default function SlideImageModal(props) {

	let slider;

	useEffect(() => {
		if (slider) {
			slider.goTo(props.selectedImageIdx, true);
		}
	}, [props.selectedImageIdx])

	return (
		<div>
			<Modal
				visible={props.showModal}
				footer={null}
				onCancel={props.onCloseModal}
				width={'90%'}
				bodyStyle={{ padding: 0, backgroundColor: 'black' }}
			>
				<Carousel autoplay 
					pauseOnHover 
					infinite 
					initialSlide={props.selectedImageIdx} 
					ref={c => slider = c} 
					arrows 
					nextArrow={<SliderArrow to="right" />} 
					prevArrow={<SliderArrow to="left" />}
				>
					{
						props.images && props.images.map(item => {
							return (
								<img src={BaseUrl + item.fullUrl} alt="img hotel" key={item.imageId} />
							)
						})
					}
				</Carousel>
			</Modal>
		</div>
	)
}

const SliderArrow = ({ className, to, onClick }) => (
	<button type="button" 
		onClick={onClick} 
		className={'slick-arrow ' + styles['slick-arrow'] + ' ' +(to === 'right' ? styles['next'] : styles['prev'])} 
		aria-label={to}
	>
		<Icon type={to} onClick={onClick} />
	</button>
)