import { getToken, clearStore } from '../helpers/AuthHelper'

export const BaseUrl ="http://103.27.238.123:1269/";

const headerDefault = {
    'Accept': 'application/json, text/plain, */*',
    'Content-Type': 'application/json'
}
function get(route, access_token = getToken()) {
    let headers = {
        'authorization': access_token,
        ...headerDefault
    };

    return fetch(BaseUrl + route, {headers})
        .then(handleStatus)
        .catch(catchError);
};

function post(route, payload, access_token = getToken()) {
    let headers = {
        'authorization': access_token,
        ...headerDefault
    };
    return fetch(BaseUrl + route, {method: 'POST', headers, body: JSON.stringify(payload)})
        .then(handleStatus)
        .catch(catchError);
};

function postFormData(route, payload, access_token = getToken()) {
    let headers = {
        'authorization': access_token
    };
    return fetch(BaseUrl + route, {method: 'POST', headers, body: payload})
        .then(handleStatus)
        .catch(catchError);
};

function put(route, payload, access_token = getToken()) {
    let headers = {
        'authorization': access_token,
        ...headerDefault
    };

    return fetch(BaseUrl + route, {method: 'PUT', headers, body: JSON.stringify(payload)})
        .then(handleStatus)
        .catch(catchError);
};

function deleteRequest(route, access_token = getToken()) {
    let headers = {
        'authorization': access_token,
        ...headerDefault
    };

    return fetch(BaseUrl + route, {method: 'DELETE', headers})
        .then(handleStatus)
        .catch(catchError);
};

function getWithoutAuth(route) {
    return fetch(BaseUrl + route, {headers: {...headerDefault}})
        .then(handleStatus)
        .catch(catchError);
};

function postWithoutAuth(route, payload) {
    return fetch(BaseUrl + route, {method: 'POST', body: JSON.stringify(payload), headers: {...headerDefault}})
        .then(handleStatus)
        .catch(catchError);
};

function catchError(err) {
	throw err.response;
}

function handleStatus(res) {
    if (!res.ok) {
        if (res.status === 401) {
            clearStore();
            const origin = window.location.origin;
            window.location.replace(origin);
        }
        throw new Error(res.statusText);
    } else {
        return res.json();
    }
}

export { get, post, put, deleteRequest, getWithoutAuth, postWithoutAuth, postFormData }
